.home-screen {
  position: relative;
  font-weight: 600;
  display: flex;
  align-items: center;
  width: 202px;
  flex-shrink: 0;
}
.icon-component-close {
  position: relative;
  width: 10px;
  height: 10px;
}
.home-screen-parent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  z-index: 0;
}
.image-7-icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: 4px;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.plugin-status-child {
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 157.17px;
  border-radius: 1px;
  width: 16.84px;
  height: 10.55px;
  z-index: 2;
}
.completed-steps {
  position: relative;
  font-weight: 500;
}
.icon-check-solid {
  position: relative;
  width: 14px;
  height: 14px;
}
.optimise-design {
  position: relative;
}
.icon-check-solid-parent {
  display: flex;
  flex-direction: row;
  padding: var(--padding-11xs) 0;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.completed-steps-parent,
.frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-parent {
  gap: var(--gap-9xs);
  font-size: var(--font-size-xs);
  color: var(--studio-darkmode-popupclick-40495e);
}
.completed-steps-parent {
  gap: 6px;
  z-index: 3;
  font-size: 11px;
  color: var(--studio-darkmode-popuplabels-5e6a86);
}
.node-status-card,
.plugin-status {
  position: relative;
  background-color: var(--color-white);
  display: flex;
}
.plugin-status {
  border-radius: 6px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.12);
  border-bottom: 0.7px solid #e9edf6;
  box-sizing: border-box;
  width: 222px;
  height: 369.83px;
  flex-direction: column;
  padding: 11px 10px 9px;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.node-status-card {
  width: 100%;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-xs);
  color: var(--studio-darkmode-popupclick-40495e);
  font-family: var(--font-inter);
}
